import React, { useEffect, useState } from 'react';
import { Link as RouterLink } from "react-router-dom";
import { Button, Typography } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';
import ReactPaginate from 'react-paginate';
import { MarketOverview } from "react-tradingview-embed";
import { connect } from "react-redux";

import Skeleton from 'react-loading-skeleton'

//Import
import { WhiteTextTypography, time_ago } from '../../constantData';
import cardPostFooterImg from "../../assets/images/card-post-footer.png"
import useStyles from "../../assets/js/useStyles";
import 'react-loading-skeleton/dist/skeleton.css'

import { GetNews } from "../../actions/news"

const itemsPerPage = 10;

const NewsPage = (props) => {
    const {
        conetent,
        cardComponent,
        cardPostDeliverd,
        inputEmail,
        subscribeButton,
        cardPostFooter,
        freeBook,
    } = useStyles();

    const [themeMode, setThemeMode] = useState('dark');
    const [newsLists, setNewListst] = useState([])

    const [currentItems, setCurrentItems] = useState([]);
    const [pageCount, setPageCount] = useState(0);
    const [totalPages, setTotalPages] = useState()
    const [itemOffset, setItemOffset] = useState(0);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        onload(pageCount, true);
    }, []);

    useEffect(() => {
        console.log(props.newsLists)
        if (props.newsLists.length != 0) {
            setIsLoading(false)
        }

    }, [props.newsLists]);



    const onload = async (pageCount, initial) => {
        console.log(props);
        const data = await props.GetNews({ limit: 7, page: pageCount });
        if (data.data) {
            const items = data.data
            onChangeOffset(items, items.total_recodes)
            setTotalPages(Math.ceil(data.total_recodes / 10))
        }
    }



    // Invoke when user click to request another page.
    const handlePageClick = (event) => {
        const newOffset = (event.selected * itemsPerPage) % newsLists.length;
        console.log(`User requested page number ${event.selected + 1}, which is offset ${newOffset}`);
        let page = (event.selected + 1)
        onload(page);
        setItemOffset(newOffset);
        onChangeOffset(newsLists)
        setIsLoading(true)
        setTimeout(() => {
            setIsLoading(false)
        }, 2000);
    };



    const onChangeOffset = (items) => {
        const endOffset = itemOffset + itemsPerPage;
        setPageCount(Math.ceil(items.length / itemsPerPage));
    }
    const mode = document.body.className == "light-mode" ? 'light' : "dark";

    if (mode != themeMode) {
        setThemeMode(mode)
    }


    const getCardLists = (propscurrentItems) => {
        return propscurrentItems.map(({ index, title, media, link, image, contents }) => {
            //const image =  (media && media.length != 0 && !media.role) ? media[0].url : "http://localhost:3000/kick/static/media/black-logo-sm-2.2b746481de2311fed3c3.png"
            const contentsData = contents.length != 0 ? contents[0].replaceAll("\n\n", "") : ""
            return (
                <RouterLink
                    to={{
                        pathname: "/newsdetail",
                        state: {
                            index,
                            title,
                            image,
                            contents,
                            contentsData,
                            link
                        }
                    }}

                    className='card-list-link'>
                    <div key={index} className='card-list'>
                        <div className="image-card" >
                            <img src={image} width="218" height="150" />
                            <div className='image-body'>
                                <p className='text-primary'>{title}</p>
                                <div style={{
                                    display: "flex",
                                    justifyContent: "space-between",
                                    fontSize: "12px"
                                }}>
                                    {/* <p><strong>source :</strong> {source["_@ttribute"]}</p>
                                    <p style={{ fontSize: "12px", float: "right" }}>{time_ago(pubDate)}</p> */}
                                </div>

                                <p className='text-secondary'> {contentsData}</p>
                            </div>
                        </div>
                    </div>
                </RouterLink>
            );
        });
    }


    const getCardLoader = () => {
        const baseColor = mode != "light" ? "#0e0e0e" : "#ebebeb";
        const highlightColor = mode != "light" ? "#061727" : "#f5f5f5";
        return [1, 2, 3, 4, 5, 6, 7, 8].map(() => {
            return (
                <div className="post">
                    <div className="left-col">
                        <div className="avatar">
                            <Skeleton
                                baseColor={baseColor}
                                highlightColor={highlightColor}
                                height="100%"
                                containerClassName="avatar-skeleton"
                            />
                        </div>
                        <div className="user-name">
                            <Skeleton
                                baseColor={baseColor}
                                highlightColor={highlightColor}
                                width={150} />
                        </div>
                    </div>
                    <div className="right-col">
                        <>
                            <Skeleton
                                baseColor={baseColor}
                                highlightColor={highlightColor}
                                height="100%"
                                width={"551px"}
                                count={1} />
                        </>
                    </div>
                </div>
            )
        })
    }
    return (
        <Container maxWidth="lg" className={conetent}>
            <main>
                <Grid container className='selection-one'>
                    <Grid item lg={8} sm={12} className='grid-one' >

                        {(isLoading) && <>{getCardLoader()}</>}


                        {(!isLoading) && <>
                            {getCardLists(props.newsLists)}
                        </>}

                        {props.newsLists.length != 0 &&
                            <ReactPaginate
                                className='pagination'
                                breakLabel="..."
                                nextLabel=""
                                onPageChange={handlePageClick}
                                pageRangeDisplayed={5}
                                pageCount={totalPages}
                                previousLabel=""
                                renderOnZeroPageCount={null}
                            />
                        }

                    </Grid>
                    <Grid item lg={4} sm={12}>
                        <div className={`${cardComponent} cardComponent`} >
                            {themeMode == mode &&
                                <MarketOverview
                                    widgetProps={{
                                        colorTheme: mode,
                                        isTransparent: true,
                                    }}
                                ></MarketOverview>
                            }
                        </div>
                        <img src={cardPostFooterImg} className={freeBook} />
                        <div className={`${cardPostDeliverd} cardPostDeliverd`}>
                            <WhiteTextTypography variant="h6" className="card-post-deliverd-title-1">
                                Get New Posts Delivered to Your Inbox
                            </WhiteTextTypography>
                            <WhiteTextTypography variant="subtitle1" className='card-post-deliverd-title-2'>
                                Subscribe to our mailing list and get interesting stuff and updates to your email inbox.
                            </WhiteTextTypography>
                            <input type="text" placeholder='Email' className={inputEmail} />
                            <Button
                                {...{
                                    color: "inherit",
                                    to: "#",
                                    component: RouterLink,
                                    className: subscribeButton,
                                }}>
                                SUBSCRIBE
                            </Button>
                            <p className={cardPostFooter}>
                                *by submitting your information you agree to the terms of our Privacy Policy. You will receive Weekly Stock Watch email and third party offers. You can unsubscribe anytime by selecting unsubscribe at the bottom of our emails.
                            </p>
                        </div>

                    </Grid>
                </Grid>
            </main>
        </Container>
    );
}

const mapStateToProps = ({ reducer }) => {
    return {
        newsLists: reducer.newsLists
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
        GetNews: (data) => dispatch(GetNews(data))
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(NewsPage);