// @flow
import * as TYPES from '../actions/types';

const INIT_STATE = {
    newsLists: []

};

const Reducer = (state = INIT_STATE, action) => {
    switch (action.type) {
        case TYPES.SET_NEWS_DATA:
            return {
                ...state,
                newsLists: (action.newsLists && action.newsLists.length) ? action.newsLists : []
            };
        default:
            return state;
    }
};

export default Reducer;
