import React, { useEffect, useState } from 'react';
import { useLocation } from "react-router";
import { Link as RouterLink } from "react-router-dom";
import { Button, Typography } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';
import ReactPaginate from 'react-paginate';
import { MarketOverview } from "react-tradingview-embed";
import { connect } from "react-redux";

import Skeleton from 'react-loading-skeleton'

//Import
import { WhiteTextTypography, time_ago } from '../../constantData';
import cardPostFooterImg from "../../assets/images/card-post-footer.png"
import useStyles from "../../assets/js/useStyles";
import 'react-loading-skeleton/dist/skeleton.css'
import amazon from "../../assets/images/amazon.png";
import { headersData } from '../../constantData';
import "./newsdetail.css";

import { GetNews } from "../../actions/news"

const itemsPerPage = 10;

const NewsDetail = (props) => {
    const {
        conetent,
        socialButton,
        socialRootButtons,
        cardComponent,
        cardPostDeliverd,
        inputEmail,
        subscribeButton,
        cardPostFooter,
        freeBook,
    } = useStyles();

    const [themeMode, setThemeMode] = useState('dark');

    useEffect(() => {

    }, []);


    const mode = document.body.className == "light-mode" ? 'light' : "dark";

    if (mode != themeMode) {
        setThemeMode(mode)
    }

    const getMenuButtons = () => {
        return headersData.map(({ label, href, Icon }) => {
            return (
                <Button
                    startIcon={<Icon />}
                    {...{
                        key: label,
                        color: "inherit",
                        to: href,
                        component: RouterLink,
                        className: `${socialButton} social-button`,
                    }}
                >
                    {label}
                </Button>
            );
        });
    };


    console.log(props.location.state);
    const state = props.location.state;
    const contentList = state.contents.length != 0 ? state.contents[0].split("\n\n") : []
    return (
        <Container maxWidth="lg" className={conetent}>
            <main>
                <Grid container className='selection-one'>
                    <Grid item lg={8} sm={12} alignItems={'center'} justifyContent={'space-between'} className='grid-one' >
                        <div>
                            <Typography variant="h6" align="left" className={'newsdeatiltitle simple-text'}>
                                {state.title}
                            </Typography>
                        </div>
                        <div className={`${socialRootButtons} social-icon`}>{getMenuButtons()}</div>
                        <div className="top-news-card">
                            <img src={state.image} className="amazon-image" />
                        </div>

                        <div className='news-detailtext simple-text'>
                            {contentList.map(item => (
                                <p>{item}</p>
                            ))}
                        </div>
                        <a href={state.link} target={'_blank'} className="simple-text">Link</a>
                    </Grid>
                    <Grid item lg={4} sm={12}>
                        <div className={`${cardComponent} cardComponent cardComponent-newsdetail `} >
                            {themeMode == mode &&
                                <MarketOverview
                                    widgetProps={{
                                        colorTheme: mode,
                                        isTransparent: true,
                                    }}
                                ></MarketOverview>
                            }

                        </div>
                        <img src={cardPostFooterImg} className={` ${freeBook} spacinfornewsdetail `} />
                        <div className={`${cardPostDeliverd} cardPostDeliverd spacinfornewsdetail`}>
                            <WhiteTextTypography variant="h6" className="card-post-deliverd-title-1">
                                Get New Posts Delivered to Your Inbox
                            </WhiteTextTypography>
                            <WhiteTextTypography variant="subtitle1" className='card-post-deliverd-title-2'>
                                Subscribe to our mailing list and get interesting stuff and updates to your email inbox.
                            </WhiteTextTypography>
                            <input type="text" placeholder='Email' className={`${inputEmail} subscribe-input`} />
                            <Button
                                {...{
                                    color: "inherit",
                                    to: "#",
                                    component: RouterLink,
                                    className: subscribeButton,
                                }}>
                                SUBSCRIBE
                            </Button>
                            <p className={cardPostFooter}>
                                *by submitting your information you agree to the terms of our Privacy Policy. You will receive Weekly Stock Watch email and third party offers. You can unsubscribe anytime by selecting unsubscribe at the bottom of our emails.
                            </p>
                        </div>
                    </Grid>
                </Grid>
            </main>
        </Container>
    );
}

const mapStateToProps = ({ reducer }) => {
    return {
        newsLists: reducer.newsLists
    };
}

const mapDispatchToProps = (dispatch) => {
    return {
        GetNews: (data) => dispatch(GetNews(data))
    };
}


export default connect(mapStateToProps, mapDispatchToProps)(NewsDetail);